class componentmanage extends Interstellar.pagesBase {
  complete() {
    this.yymcdata = [{
      val: '1',
      idx: '1'
    }, {
      val: '2',
      idx: '2'
    }]
    this.action = {
      config: {
        dis: 'inline',
        link: 'noLink',
        content: [{text: '', key: 'view'}, {text: '', key: 'edit'}, {text: '', key: 'copy'}, {
          text: '',
          key: 'editlabel'
        }]
      }
    }
    this.action1 = {
      config: {
        dis: 'inline',
        link: 'noLink',
        content: [{text: '', key: 'view'}, {text: '', key: 'copy'}, {text: '', key: 'editlabel'}, {
          text: '',
          key: 'correct'
        }]
      }
    }
    this.apidata = {}
    this.apidata.pageSize = 10;
    this.apidata.page = 1;
    this.render();
    this.setTitle();
    this.resize();
    this.component = require("../modal/createcomponent/createcomponent")
    this.formsubmit = require("../modal/formsubmit/formsubmit")
    this.dom.find('.createbtn').on('click', () => {
      let newcomponent = this.app.loadModal(this.component, {adv: true}, {type: 'new'})
      newcomponent.event._addEvent('component.new',async (value) => {
        this.app.loading.show()
        let res = await this.api.create(value.data)
          this.app.loading.hide()
          if (res.code == 0) {
            newcomponent.close();
            this.apidata.page = 1;
            this.setMain(true);
            this.app.changePage('componentmanage')
          } else {
            alert('')
          }
      })
      newcomponent.event._addEvent('component.createlabel',async (value) => {
        let json = {
          category: 'component',
          text: value.data.val
        }
        this.app.loading.show()
        let res = await this.api.createlabel(json)
          this.app.loading.hide()
          if (res.code == 0) {
            newcomponent.createlabel(res, value);
          }
      })
      newcomponent.event._addEvent('component.inputlabel',async (value) => {
        let json = {
          pageSize: 10,
          category: 'component',
          text: value.data.val
        }
        let res = await this.api.searchlabel(json)
          if (res.code == 0) {
            newcomponent.showlabel(res);
          }
      })
    })
    this.dom.find('.searchbtn').on('click', () => {
      this.apidata.name = this.dom.find('.name').val();
      this.apidata.text = this.dom.find('.label').val();
      this.apidata.page = 1;
      this.setMain(true)
    })
  }

  render() {
    require.ensure("../moduleslibs/dropdown1/drop.js", () => {
      let dropdown = require("../moduleslibs/dropdown1/drop.js")
      this.type1 = this.app.loadModule(dropdown, this.dom.find('.kjlx'), {
        className: 'xlk',
        firstSelect: {
          val: '',
          idx: ''
        },
        data: [{
          val: '',
          idx: 'text'
        }, {
          val: '',
          idx: 'select'
        }, {
          val: '',
          idx: 'radiobox'
        }, {
          val: '',
          idx: 'checkbox'
        }]
      })
      this.type1.event._addEvent('option.click', (value) => {
        this.apidata.type = value.idx
      })
      this.type1.event._addEvent('dropDown.clear', (value) => {
        this.apidata.type = ''
      })
    })
  }

  setTitle() {
    let obj = {}
    obj['icon'] = {
      "code": {
        name: '<span data-i18n="age" data-name=""></span>',
        type: 'text',
        code: 'checkid',
        w: '20%',
        ww: '20%',
        n: "40"
      },
      "name": {
        name: '<span data-i18n="age" data-name=""></span>',
        type: 'text',
        code: 'checkid',
        w: '20%',
        ww: '20%',
        n: "40"
      },
      "type": {
        name: '<span data-i18n="age" data-name=""></span>',
        type: 'text',
        code: 'pid',
        w: '20%',
        ww: '20%',
      },
      "editable": {
        name: '<span data-i18n="age" data-name=""></span>',
        type: 'text',
        code: 'pid',
        w: '20%',
        ww: '20%',
      },
      "label": {
        name: '<span data-i18n="age" data-name=""></span>',
        type: 'text',
        code: 'pname',
        w: '20%',
        ww: '20%'
      },
    };
    obj['tablewidth'] = ES.selctorDoc('.componentmanage').box().clientWidth - 140;
    obj['type'] = 'center';
    obj['actionicon'] = {
      "operation": {
        name: '<span data-i18n="action" data-name=""></span>',
        type: 'action',
        code: 'action',
        w: '100%',
        ww: '100%'
      }
    };
    require.ensure("../moduleslibs/table_group/table_group", () => {
      let cont_table = require("../moduleslibs/table_group/table_group")
      this.table = this.app.loadModule(cont_table, this.dom.find('.componenttable'), {
        id: 'componenttable',
        header: obj
      });
      this.table.event._addEvent('table.pagenumber', (value) => {
        this.apidata.page = parseInt(value);
        this.table.changenum(this.apidata.page);
        this.setMain();
      });
      this.table.event._addEvent('table.action',async (value) => {
        switch (value.classname) {
          case 'view':
            this.getcomdetail(value.id, 'view');
            break;
          case 'edit':
            this.getcomdetail(value.id, 'edit');
            break;
          case 'editlabel':
            this.getcomdetail(value.id, 'editlabel');
            break;
          case 'correct':
            this.getcomdetail(value.id, 'correct');
            break;
          case 'copy':
            this.app.loading.show()
            let res = await this.api.formComponent_clone({id: value.id})
            this.app.loading.hide()
              if (res.code == 0) {
                this.apidata.page = 1;
                this.setMain(true);
              } else {
                this.app.alert.show({
                  title: ' ',
                  msg: '',
                  close: true
                })
              }
        }
      });
      this.table.event._addEvent('table.pagesize', (value) => {
        this.apidata.pageSize = value.num;
        this.apidata.page = 1;
        this.setMain(true);
      });
      this.dom.find('.list-content').removeClass('hide');
      this.apidata.page = 1;
      this.resize()
      this.setMain(true);
    })
  }

  async getcomdetail(value, type) {
    let json = {
      id: value
    }
    this.app.loading.show()
    let res = await this.api.getdetails(json)
    this.app.loading.hide()
      if (res.code == 0) {
        if (type == 'edit' || type == 'editlabel') {
          let editcomponent = this.app.loadModal(this.component, {adv: true}, {
            type: type,
            data: res.data,
            componentid: value
          })
          editcomponent.event._addEvent('component.edit', async (value) => {
            this.app.loading.show()
            let res = await this.api.update(value.data)
            this.app.loading.hide()
              if (res.code == 0) {
                editcomponent.close();
                this.apidata.page = 1;
                this.setMain(true);
              } else {
                alert('')
              }
            })
          editcomponent.event._addEvent('component.createlabel',async (value) => {
            let json = {
              category: 'COMPONENT',
              text: value.data.val
            }
            this.app.loading.show()
            let res = await this.api.createlabel(json)
              this.app.loading.hide()
              if (res.code == 0) {
                editcomponent.createlabel(res, value);
              }
          })
          editcomponent.event._addEvent('component.inputlabel',async (value) => {
            let json = {
              pageSize: 10,
              category: 'COMPONENT',
              text: value.data.val
            }
            let res = await this.api.searchlabel(json)
            if (res.code == 0) {
              editcomponent.showlabel(res);
            }
          })
        } else if (type == 'view') {
          let viewcomponent = this.app.loadModal(this.component, {adv: true}, {type: 'view', data: res.data})
        } else if (type == 'correct') {
          let config = [{
            name: 'correctionPassword',
            type: 'input',
            title: ':',
            value: '',
            check: 'empty',
            remark: '',
            inputtype: 'password'
          }]
          let keyinput = this.app.loadModal(this.formsubmit, {adv: true}, {config: config, title: ''})
          keyinput.event._addEvent('formsubmit.submit',async (data) => {
            data.userId = JSON.parse(this.app.local.get('all')).userId
            let res1 = await this.api.formComponent_correction(data)
              if (res1.code == 0) {
                keyinput.close()
                let editcomponent = this.app.loadModal(this.component, {adv: true}, {
                  type: type,
                  data: res.data,
                  componentid: value
                })
                editcomponent.event._addEvent('component.edit',async (value) => {
                  let res = await this.api.update(value.data)
                  this.app.loading.hide()
                    if (res.code == 0) {
                      editcomponent.close();
                      this.apidata.page = 1;
                      this.setMain(true);
                    } else {
                      alert('')
                    }
                  })
                editcomponent.event._addEvent('component.createlabel',async (value) => {
                  let json = {
                    category: 'COMPONENT',
                    text: value.data.val
                  }
                  this.app.loading.show()
                  let res = await this.api.createlabel(json)
                    this.app.loading.hide()
                    if (res.code == 0) {
                      editcomponent.createlabel(res, value);
                    }
                })
                editcomponent.event._addEvent('component.inputlabel',async (value) => {
                  let json = {
                    pageSize: 10,
                    category: 'COMPONENT',
                    text: value.data.val
                  }
                  let res = await this.api.searchlabel(json)
                    if (res.code == 0) {
                      editcomponent.showlabel(res);
                    }
                })
              } else if (res1.code == 1071) {
                keyinput.showerror({correctionPassword: ''})
              }
            })
        }
      }
  }

  async setMain(bool) {
    let data2 = []
    this.table.showloading()
    let res = await this.api.querylist(this.apidata)
      if (res.code == 0) {
        if (res.data.list.length > 0) {
          res.data.list.forEach((val, idx) => {
            let obj = {}
            obj.id = val.id;
            data2.push(obj)
            val.label = '';
            val.tagList.forEach(function (value, index) {
              if (index < val.tagList.length - 1) {
                val.label += value.text + ','
              } else {
                val.label += value.text
              }
            });
            switch (val.type) {
              case 'text':
                val.type = '';
                break;
              case 'select':
                val.type = '';
                break;
              case 'checkbox':
                val.type = '';
                break;
              case 'radiobox':
                val.type = '';
                break;
            }
            if (val.editable) {
              obj.operation = this.action
              val.editable = ''
            } else {
              obj.operation = this.action1
              val.editable = ''
            }
          })
          this.table.setData(res.data.list, data2)
        } else {
          this.table.noData();
        }

      }
      if (bool) {
        this.table.getTotal(res.data.pages, 2, res.data.total)
      }
    this.initscroll();
  }

  initscroll() {
    if (this.myScroll) {
      this.myScroll.refresh()
      return
    }
    var rid = 'aaa_' + Math.floor(new Date().getTime() * Math.random())
    this.dom.find('.scrolltable').attr('id', rid)
    this.myScroll = new IScroll('#' + rid, {
      scrollbars: true,
      mouseWheel: true,
      interactiveScrollbars: true,
      hideScrollbar: false,
      vScrollbar: true,
      shrinkScrollbars: 'scale',
      fadeScrollbars: false,
      disableMouse: true,
      disablePointer: true
    });
  }

  resize() {
    let ch = ES.selctorDoc(window).box().clientHeight - 100
    let cw = ES.selctorDoc(window).box().clientWidth - 240
    ES.selctorDoc('.componentmanage').css({'height': ch, 'width': cw});
    ES.selctorDoc('.scrolltable').css({'height': ch - 200});
  }
}

module.exports = componentmanage;



